* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: courier, monospace;
  background-color: #f5f5f5;
  width: 600px;
  margin: 0 auto;
  padding: 0;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border-radius: 5px;
  background-color: #f5f5f5;
  padding: 30px;
  border: 2px solid #dedede;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px 10px 0px 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  border-left: 5px solid green;
  color: #555;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  text-transform: capitalize;
  border-bottom: 2px dotted #ccc;
}

.task.reminder {
  border-left: 5px solid red;
  border-bottom: 2px dotted #ccc;
  text-indent: 5px;
  height: auto;
  padding: 10px 20px;
  text-transform: capitalize;
  text-decoration: line-through;
}

.task:hover {
  background-color: #e2dcdc;
  transition: all 0.2s;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}
